}
100%{-webkit-transform:rotate(720deg);transform:rotate(720deg)
}
}
@keyframes preloader-data-v-2fd409e4{
0%{-webkit-transform:rotate(0);transform:rotate(0)
}
60%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
100%{-webkit-transform:rotate(720deg);transform:rotate(720deg)
}
}
.loader[data-v-2fd409e4]{display:block;width:100%;height:100%;z-index:5;border-radius:2px;background:rgba(255,255,255,0.6);position:absolute
}
.loader .spinner[data-v-2fd409e4]{position:absolute;top:50%;left:50%;margin:-9px 0 0 -9px;display:block;width:18px;height:18px;border-radius:50%;border-color:inherit;box-shadow:inset 0 0 0 2px #fff;-webkit-animation:preloader-data-v-2fd409e4 2s cubic-bezier(0.25, 0.625, 0.47, 0.915) infinite;animation:preloader-data-v-2fd409e4 2s cubic-bezier(0.25, 0.625, 0.47, 0.915) infinite
