}
.loader .spinner[data-v-2fd409e4]:before{-webkit-animation:preloader-data-v-2fd409e4 3s cubic-bezier(0.25, 0.625, 0.47, 0.915) infinite;animation:preloader-data-v-2fd409e4 3s cubic-bezier(0.25, 0.625, 0.47, 0.915) infinite
}
.loader .spinner[data-v-2fd409e4]:after{-webkit-animation:preloader-data-v-2fd409e4 3s cubic-bezier(0.625, 0.295, 0.81, 0.49) infinite;animation:preloader-data-v-2fd409e4 3s cubic-bezier(0.625, 0.295, 0.81, 0.49) infinite
}



